<template>
<body>
  <button id="startSpeech" @click="recognitionToggle()">Recognition</button>
</body>
</template>

<script>
const request = function(method, url, body) {
  return fetch(url, {
    method: method,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${this.state.token}`
    },
    body: JSON.stringify({name: "name", body: body})
  })
};

const speechRecognition = () => {
  if ("SpeechRecognition" in window) {
    return new SpeechRecognition();
  } else if ("webkitSpeechRecognition" in window) {
    return new webkitSpeechRecognition();
  } else {
    return undefined;
  }
}

const recognition = speechRecognition();

recognition.lang = "ja-JP";
recognition.onstart = function() {
}

recognition.onresult = function(event) {
  alert(event.results[0][0].transcript);
}

recognition.onerror = function(event) {
  this.words = request("GET", "http://localhost:1145/word").then(successHandler).catch(errorHandler)
}

let is_active = false;
export default {
  methods: {
    recognitionToggle () {
      if ( is_active === false ) {
        is_active = true;
        recognition.start();
      }
      else{
        is_active = false;
      }
    }
  }
}
</script>
